@use "vars" as *;
.sec-periodontal{
	background: white;
	border-radius: 100px 100px 0 0;
	padding: 100px 0 137px;
	@include mq(){
		border-radius: 50px 50px 0 0;
		padding: 60px 0 80px;
	}
}

.periodontal-detail{
	max-width: 860px;
	margin: 0 auto;
	@include fts(16,28.8);
	letter-spacing: 0.8px;
	@include mq(){
		@include fts(15,27);
	}
	a{
		color: #3E7084;
		text-decoration: underline;
	}
	strong{
		display: block;
		@include font-size(18);
		font-weight: bold;
		margin-bottom: 7px;
		@include mq(){
			@include font-size(16);
			padding: 8px 0 0;
			margin-bottom: 2px;
		}
	}
	img{
		width: 100%;
		height: auto;
		margin-bottom: 32px;
	}
	p {
		margin-bottom: 10px;
	}
	&__item{
		margin-bottom: 105px;
		@include mq(){
			margin-bottom: 76px;
			&--other{
				p{
					margin-bottom: 34px;
				}
			}
		}
		&:last-child{
			margin-bottom: 0;
		}
	}
	&--count{
		counter-reset: section;
		&__item{
			.c-ttl__04{
				padding-left: 25px;
				@include mq(){
					padding-left: 20px;
				}
				&:before{
					counter-increment: section;
					content: counters(section, ".") ".";
					position: absolute;
					top: 30px;
					left: 0;
					@include mq(){
						top: 11px;
					}
				}
			}
		}
	}
	&--ol{
		counter-reset: section;
		ol{
			margin-bottom: 26px;
			li{
				position: relative;
				padding: 0 0 0 24px;
				margin-bottom: 4px;
				&:before{
					counter-increment: section;
					content: counters(section, ".") ".";
					position: absolute;
					top: 0;
					left: 0;
					text-align: right;
					width: 23px;
				}
			}
		}
	}
}