
@use "vars" as *;

.clinic-list{
	@include flex(0,0,wrap);
	margin: 0 -10px;
	@include mq(){
		&.js-clinic-list{
			margin: 0 -20px;
			.clinic-list__item{
				padding: 0 5px;
				margin-bottom: 0;
			}
			.slick-list{
				padding: 0 6% !important;
			}
			.slick-slide{
				padding: 0 5px;
			}
			.slick-arrow{
				display: none !important;
			}
			.slick-dots{
				position: static;
				@include flex(center,center);
				padding: 10px 0 0;
				li{
					height: 10px;
					width: 10px;
					button{
						width: 10px;
						height: 10px;
						border-radius: 50%;
						border: none;
						background: $pink;
						&:before{
							display: none;
						}
					}
					&.slick-active{
						button{
							background: $blue;
						}
					}
				}
			}

		}
		&.clinic-list--02{
			margin: 0 -5px;
			.clinic-list__item{
				padding: 0 5px;
				margin-bottom: 18px;
			}

			.clinic-list__ct{
				@include fts(12,16.8);
				padding: 10px 13px;
				&-ttl{
					@include font-size(14);
					padding: 0 0 7px;
					margin-bottom: 9px;
				}
			}
		}
	}
	@include mq(sm){
		margin: 0;
	}
	&__item{
		width: 25%;
		padding: 0 10px;
		margin-bottom: 70px;
		@include mq(lg){
			width: 50%;
		}
		@include mq(sm){
			margin-bottom: 40px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	&__box{
		border: 1px solid #E6ECEE;
		height: 100%;
	}
	&__img{
		img{
			width: 100%;
		}
	}
	&__ct{
		padding: 20px;
		letter-spacing: 1px;
		@include fts(15,24);
		&-ttl{
			display: block;
			@include font-size(18);
			line-height: 1.4;
			font-weight: 500;
			text-align: center;
			position: relative;
			padding: 0 0 14px;
			margin-bottom: 14px;
			&:after{
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				width: 10px;
				height: 2px;
				margin: 0 auto;
				background: #7AB1CD;
			}
		}
	}
}

.sec-hospital{
	background: white;
	padding: 0 0 50px;
}
.sec-facility{
	padding: 38px 0 80px;
	position: relative;
	@include mq(){
		padding: 38px 0 62px;
	}
	&:before{
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		height: 440px;
		background: white;
		@include mq(){
			height: 200px;
		}
	}
	.inner{
		position: relative;
		z-index: 1;
	}
}
.facility-heading{
	text-align: center;
	letter-spacing: 1px;
	margin-bottom: 60px;
	@include mq(){
		margin-bottom: 32px;
	}
	img{
		width: 640px;
		margin: 0 auto 38px;
		@include mq(){
			margin: 0 auto 32px;
		}
	}
	&__ttl{
		display: block;
		@include font-size(26);
		font-weight: bold;
		margin-bottom: 4px;
		@include mq(){
			@include font-size(22);
		}
	}
}

.sec-staff{
	background: white;
	padding: 0 0 150px 0;
	@include mq(){
		padding: 26px 18px 11px;
	}
}
.staff-list{
	&__item{
		@include flex(0,0,wrap);
		border: 1px solid #E6ECEE;
		position: relative;
		padding: 32px 29px;
		margin-bottom: 30px;
		@include mq(){
			padding: 26px 18px 11px;
		}
		&:last-child{
			margin-bottom: 0;
		}
		&:before{
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			height: 144px;
			background: #F8F9F9;
			@include mq(){
				height: 333px;
			}
		}
	}
	&__img{
		width: 230px;
		position: relative;
		z-index: 1;
		@include mq(sm){
			width: 170px;
			margin: 0 auto 24px;
		}
	}
	&__ct{
		width: calc(100% - 230px);
		padding: 5px 0 0 29px;
		letter-spacing: 1px;
		position: relative;
		z-index: 1;
		@include mq(sm){
			width: 100%;
			padding: 0;
		}
		&-ttl{
			display: block;
			line-height: 1.4;
			@include font-size(22);
			font-weight: bold;
			margin-bottom: 14px;
			span,small{
				display: block;
			}
			small{
				@include font-size(14);
				font-weight: 400;
				color: #AFB0B3;
				margin-bottom: 3px;
			}
		}
	}
}
.tag-list{
	@include flex(0,0,wrap);
	margin-bottom: 32px;
	@include mq(){
		margin-bottom: 39px;
	}
	li{
		line-height: 1.4;
		padding: 0 15px 0 0;
		margin: 0 13px 10px 0;
		font-size: 1.5rem;
		position: relative;
		letter-spacing: 0;
		@include mq(){
			@include font-size(13);
			padding: 0 13px 0 0;
			margin: 0 13px 2px 0;
		}
		a,span{
			@include font-size(15);
			color: #161616;
			@include mq(){
				@include font-size(13);
			}
		}
		&:after{
			content: "";
			width: 1px;
			position: absolute;
			top: 1px;
			right: 0;
			bottom: 4px;
			background: #161616;
		}
	}
}