@use "vars" as *;
.sec-price{
	background: white;
	border-radius: 100px 100px 0 0;
	padding: 100px 0;
	@include mq(){
		border-radius: 50px 50px 0 0;
		padding: 60px 0 80px;
	}
}

.price-list{
	@include flex(between,0,wrap);
	&__item{
		width: calc(50% - 20px);
		border: 1px solid #E6ECEE;
		margin-bottom: 43px;
		@include mq(){
			width: 100%;
			margin-bottom: 22px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	&__ttl{
		display: block;
		@include font-size(18);
		font-weight: bold;
		line-height: 1.4;
		background: #F8F9F9;
		padding: 15px 30px;
		letter-spacing: 1px;
		@include mq(){
			text-align: center;
			padding: 15px 23px;
		}
		@include mq(xs){
			padding: 15px 10px;
		}
	}
	&__ct{
		padding: 23px 30px;
		line-height: 1.352;
		letter-spacing: 1px;
		@include mq(){
			padding: 10px 18px 20px;
			line-height: 1.4;
		}
		p{
			margin-bottom: 15px;
			&:last-child{
				margin-bottom: 0;
			}
		}
		table{
			width: 100%;
			tr{
				border-bottom: 1px dashed #E6ECEE;
				td{
					padding: 10px 0;
					&:nth-child(2){
						width: 125px;
						text-align: right;
						@include mq(){
							width: 110px;
						}
						span{
							display: block;
							margin-bottom: 2px;
							&:last-child{
								margin-bottom: 0;
							}
							small{
								@include font-size(12);
							}
						}
					}
				}
			}
		}
	}
}

.sec-difference {
	padding: 10px 0 56px;
	@include mq(){
		padding: 10px 0 78px;
	}
	&:before{
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		height: 355px;
		background: white;
		@include mq(){
			height: 215px;
		}
	}
	.inner{
		position: relative;
		z-index: 1;
	}
}

.difference-block{
	max-width: 800px;
	margin: 0 auto;
	&__img{
		max-width: 640px;
		margin: 0 auto 62px;
		@include mq(){
			margin: 0 auto 30px;
		}
	}
	&__ct{
		line-height: 1.8;
		letter-spacing: 1px;
		@include mq(){
			letter-spacing: 0.5px;
		}
		p{
			margin-bottom: 10px;
			@include mq(){
				margin-bottom: 0;
			}
			&:last-child{
				margin-bottom: 0;
			}
		}
		&-note{
			background: white;
			@include fts(15,21);
			color: #B78A4D;
			margin: 20px 0 23px;
			padding: 22px 20px 20px;
			@include mq(){
				@include fts(14,20);
				margin: 8px 0 9px;
			}
			p{
				position: relative;
				padding: 0 0 0 22px;
				margin-bottom: 10px;
				&:last-childơ{
					margin-bottom: 0;
				}
				&:before{
					content: "※";
					position: absolute;
					top: 0;
					left: 0;

				}
			}
		}
	}
}


.sec-payment{
	background: white;
	padding: 153px 0;
	@include mq(){
		padding: 85px 0 80px;
	}
	.heading-note{
		max-width: 790px;
		margin: 0 auto 40px;
	}
}
.payment-list{
	@include flex(center,0,wrap);
	margin: 0 -20px 90px;
	@include mq(){
		margin-bottom: 34px;
	}
	&__item{
		width: 33.333%;
		padding: 0 20px;
		position: relative;
		@include mq(){
			width: 100%;
			margin-bottom: 30px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	.c-txt__note{
		width: 100%;
		@include mq(){
			padding: 0 20px;
			letter-spacing: 1px;
			margin: -20px 0 30px;
		}
	}
	&__box{
		border: 1px solid #E6ECEE;
		text-align: center;
		position: relative;
		height: 100%;
		letter-spacing: 1px;
		padding: 37px 20px 14px;
		@include mq(){
			padding: 29px 20px 20px;
			letter-spacing: 0;
		}
		>*{
			position: relative;
			z-index: 2;
		}
		&:before,&:after{
			content: "";
			position: absolute;
			top: -1px;
			bottom: -1px;
			width: 20%;
			background: white;
		}
		&:before{
			left: 15%;
		}
		&:after{
			left: 65%;
		}
		&-icon{
			width: 40px;
			margin: 0 auto 10px;
			position: static;
			@include mq(){
				width: 30px;
			}
			&:before{
				content: "";
				height: 50%;
				position: absolute;
				@include v-align;
				left: -1px;
				right: -1px;
				background: white;
			}
			img{
				position: relative;
				z-index: 1;
			}
		}
		&-ttl{
			display: block;
			@include font-size(16);
			font-weight: 500;
			margin-bottom: 10px;
		}
		p{
			margin-bottom: 6px;
			@include mq(){
				margin-bottom: 7px;
			}
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}
.payment-note{
	@include fts(18,32);
	text-align: center;
	font-weight: 500;
	letter-spacing: 1px;
	margin-bottom: 37px;
	@include mq(){
		@include fts(16,27);
		margin-bottom: 25px;
	}
}
.payment-box{
	max-width: 860px;
	margin: 0 auto;
	border: 1px solid #E6ECEE;
	@include fts(15,27);
	padding: 32px 39px 37px;
	letter-spacing: 1px;
	@include mq(){
		@include fts(14,25);
		padding: 32px 20px 11px;
		letter-spacing: 0.5p
	}
	a{
		color: $blue;
		text-decoration: underline;
	}
	&__ttl{
		display: block;
		@include font-size(18);
		text-align: center;
		font-weight: 500;
		margin-bottom: 12px;
		@include mq(){
			@include font-size(16);
			margin-bottom: 6px;
		}
	}
}